<script>
export default {
  name: 'app',
  mounted () {
    this.loadData()
  },
  data () {
    return {
      msg: '',
      level: '', // 密码强度等级
      verifyCodeText: '获取验证码', // 验证码按钮显示内容
      interval: null, // 验证码码定时器
      countdown: '', // 验证码倒计时秒数
      countdownshow: false, // 验证码倒计时秒数显示标志
      formBindPhone: {
        phone: '',
        verify: ''
      },
      formPwd: {
        oldPwd: '',
        newPwd: '',
        confirmPwd: ''
      },
      rules: {
        phone: [
          { required: true, message: '请输入手机号码', trigger: 'blur' },
          { min: 11, max: 11, message: '手机号不完整', trigger: 'blur' }
        ],
        verify: [
          { required: true, message: '请输入短信验证码', trigger: 'blur' }
        ],
        oldPwd: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ],
        newPwd: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ],
        confirmPwd: [
          { required: true, message: '请输入确认密码', trigger: 'blur' }
        ],
        inviteCode: [
          { required: true, message: '请输入邀请码', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    loadData () {
      this.$http.post('/user/queryuserinfo', {}, res => {
        if (res.success) {
          console.log(res.data)
        } else {
          // 返回错误的处理
        }
      })
    },
    onSuccess () {
      let me = this
      function hasSend () {
        me.msg = '验证成功,请注意查收短信。'
        me.countdown = 60
        me.countdownshow = true
        // 开始计时
        me.interval = setInterval(() => {
          me.countdown--
          if (me.countdown <= 0) {
            me.verifyCodeText = '重新发送'
            me.countdownshow = false
            clearInterval(me.interval)
          }
        }, 1000)
      }
      this.$http.post('/user/vcfraudprevent', {}, res => {
        if (res.success) {
          this.$http.post('/user/sendsmsverificationcode', {
            phone: me.formBindPhone.phone,
            msgType: 'modcellphone',
            sign: res.data.sign
          }, res => {
            if (res.success) {
              hasSend()
            } else {
              // 返回错误的处理
            }
          })
        } else {
          // 返回错误的处理
        }
      })
    },
    updateBindPhone (formName) {
      let me = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          me.$http.post('/user/modifycellphone', {
            'newCellphone': me.formBindPhone.phone,
            'smsVerificationCode': me.formBindPhone.verify
          }, res => {
            if (res.success) {
              console.log(res.data)
            } else {
              // 返回错误的处理
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    updatePwd (formName) {
      let me = this
      console.log(me.formPwd)
      this.$refs[formName].validate((valid) => {
        if (valid) {
          me.$http.post('/user/modifypassword', {
            oldPassword: me.formPwd.oldPwd,
            newPassword: me.formPwd.newPwd
          }, res => {
            if (res.success) {
              me.$message('修改密码成功')
            } else {
              // 返回错误的处理
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

